.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
	margin: 0;
	padding: 0;
	background-color: #007bff ;
	font-family: 'Raleway', sans-serif;
}

#root {
	width: 300px;
	margin: 0 auto;
	background-color: #52BCAA;
	border-radius: 20px;
	padding: 10px;
}

.flex-row {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

.flex-col {
	display: flex;
	flex-direction: column;
}

.col-2 {
	width: 50%;
}

ul {
	list-style: none;
	padding: 0;
	width: 100%;
}

